export class PNGs {
    URLs;
    images: Array<HTMLImageElement>;
    constructor() {
        this.URLs = ["data:image/png;base64,",
            "data:image/png;base64,",
            "data:image/png;base64,",
            "data:image/png;base64,",
            "data:image/png;base64,",
            "data:image/png;base64,",
            "data:image/png;base64,",
            "data:image/png;base64,",
            "data:image/png;base64,",
            "data:image/png;base64,",
            "data:image/png;base64,",
            "data:image/png;base64,",
            "data:image/png;base64,",
            "data:image/png;base64,",
            "data:image/png;base64,"
        ];
        this.images = [];
        this.init();
    }
    init() {
        for (let i = 0; i < this.URLs.length; i++) {
            let tmp = new Image();
            tmp.onload = () => this.images.push(tmp);
            tmp.src = this.URLs[i];
        }
    }
}